import React, { Component } from 'react';

//Style
import './ErrorOther.scss';
import { colors } from 'consts/colors';

//Images
import RickRoll from 'assets/img/error.png';

//Icônes
import { ReactComponent as FileTimesSolid } from 'assets/svg/fontawesome-5-pro/file-times-solid.svg';

type Props = { message: string };
type State = {};

class ErrorOther extends Component<Props, State> {

  render() {
    return (
      <div>
        <div className="alert alert-great-danger invalid-error--header mt-3" role="alert">
          <FileTimesSolid className="me-2" height="28px" width="28px" color={colors.active} />
          Ce code QR a généré une erreur
        </div>

        <div className="card">
          <div className="card-header">
            Une erreur s'est produite durant la validation
          </div>

          <div className="card-body fs-6">
            QR Québec n'a pas réussi à valider ce code QR.
            <br />
            <div className="text-center">
              <img src={RickRoll} alt="Erreur" />
            </div>
          </div>

          <div className="card-footer">
            <div className="text-qrqc fw-bold">Erreur technique:</div>
            <div className="text-muted fs-6 mt-1">{this.props.message}</div>
          </div>
        </div>

        <br />
      </div>
    );
  }
}

export default ErrorOther;